<template>
  <div id="app">

    <header class="page-header">
      <div class="container">
        <div class="row">
          <div class="page-logo">
            <router-link to="/">
              <img src="./assets/img/logo_001.png" alt="">
            </router-link>
          </div>

          <div class="navLangChoose">
            <div class="wrapper">
              <div class="nav-item" :class='{active: index == nowIndex}' v-for='(tabItem,index) in tabParams'
                   @click='tabToggle(index)'>
                <span :class='{dropdownBtn: index == 0}' @click='dropdown'>{{tabItem}}</span>
                <ul v-if="index==0" class="dropdownWrapper" v-show='dropdownActive'>
                  <li @click="changeLangToEn">English</li>
                  <li @click="changeLangToZh">中文</li>
                  <li @click="changeLangToIt">Italiano</li>
                </ul>
              </div>
            </div>
          </div>

          <nav class="page-nav">
            <div class="page-nav__toggle" @click="navToggle">
              <span></span>
              <span></span>
              <span></span>
            </div>
            <ul class="page-nav__list" v-if="navActive" @click="navToggle">
              <li class="page-nav__item">
                <router-link to="/">
                  <span>{{$t('navText.home')}}</span>
                </router-link>
              </li>
              <li class="page-nav__item">
                <router-link to="/committee">
                  <span>{{$t('navText.committee')}}</span>
                </router-link>
              </li>
              <li class="page-nav__item">
                <router-link to="/">
                  <span>{{$t('navText.competition')}}</span>
                </router-link>
              </li>
              <li class="page-nav__item">
                <router-link to="/">
                  <span>{{$t('navText.constitution')}}</span>
                </router-link>
              </li>
              <li class="page-nav__item">
                <router-link to="/">
                  <span>{{$t('navText.touristic')}}</span>
                </router-link>
              </li>
              <li class="page-nav__item">
                <router-link to="/about">
                  <span>{{$t('navText.about')}}</span>
                </router-link>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </header>

    <div class="clearfix"></div>

    <div class="homeBanner">
      <img class="homeBannerImg" src="./assets/img/banner_02.jpg" alt="">
    </div>

    <router-view></router-view>

    <footer>
      <div class="contact">
        <div class="container">
          <div class="row">
            <div>
              <h1>Contact Us</h1>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-md-4">

            </div>
            <div class="col-xs-12 col-md-4">

            </div>
          </div>

          <div class="row">
            <div class="col-xs-12 col-md-4">

            </div>
            <div class="col-xs-12 col-md-4">

            </div>
          </div>

          <div class="row">
            <div class="col-xs-12 col-md-4">

            </div>
            <div class="col-xs-12 col-md-4">

            </div>
          </div>

        </div>
      </div>
    </footer>

    <div class="companyRights">
      <span>Copyright © www.XXXX.com. All Rights Reserved.</span>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'App',
    data() {
      return {
        screenWidth: document.body.clientWidth,
        nowIndex: 0,
        navActive: true,
        dropdownActive: false,
        tabParams: ['Translations >'],
      }
    },
    created: function () {
      if (this.screenWidth <= 996) {
        this.navActive = false;
      } else {
        this.navActive = true;
      }
    },
    methods: {
      navToggle: function () {
        if (this.screenWidth <= 996) {
          this.navActive = !this.navActive;
        }
      },
      dropdown: function () {
        if (event.target.getAttribute('class') === 'dropdownBtn') {
          this.dropdownActive = !this.dropdownActive;
        }
      },
      tabToggle: function (index) {
        this.nowIndex = index;
        if (index === 0) {
          return
        } else {
          this.dropdownActive = false;
        }
      },
      changeLangToZh: function () {
        this.$i18n.locale = 'zh';
        this.dropdownActive = false;
      },
      changeLangToEn: function () {
        this.$i18n.locale = 'en';
        this.dropdownActive = false;
      },
      changeLangToIt: function () {
        this.$i18n.locale = 'it';
        this.dropdownActive = false;
      }
    },
  }
</script>

<style scoped>
  /*导航栏*/
  .page-header {
    position: relative;
    /*height: 80px;*/
    background-color: #24292e;
    color: #fff;
    line-height: 78px;
  }

  .page-logo,
  .page-nav,
  .page-nav__list,
  .page-nav__toggle,
  .page-nav__item {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
  }

  .page-nav {
    /*height: 78px;*/
    float: right;
  }

  .page-nav__list {
    margin: 0;
    padding-left: 0;
    list-style: none;
  }

  .page-nav__item span {
    display: flex;
    flex-direction:;
    font-size: 14px;
    padding: 0 8px;
    color: #fff;
    cursor: pointer;
    transition: all .3s ease-in-out;
  }

  .page-nav__item span:hover {
    background-color: #000;
  }

  .page-nav__toggle span {
    display: block;
    height: 3px;
    width: 30px;
    margin-bottom: 5px;
    background-color: #fff;
  }

  .page-nav__toggle {
    display: none;
  }

  @media screen and (max-width: 996px) {
    .page-nav__toggle {
      position: absolute;
      top: -35px;
      right: 0;
      display: inline-block;
      float: right;
      height: 50px;
      padding-right: 13px;
    }

    .page-nav__item {
      /*max-height: 0;*/
      width: 100%;
      background-color: #323232;
      line-height: 40px;
      overflow: hidden;
      transition: max-height .25s ease-in-out;
    }

    .page-header {
      height: 50px;
      line-height: 50px;
    }

    .page-nav {
      position: absolute;
      width: 100%;
      top: 50px;
      left: 0;
    }

    .page-nav__list {
      width: 100%;
    }

    .page-nav__item span {
      width: 100%;
    }

    .page-logo {
      vertical-align: top;
    }

    .page-logo img {
      width: 40px;
    }
  }

  /*语言切换*/
  .wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .wrapper > div {
    flex: 1;
    text-align: center;
    height: 36px;
    line-height: 36px;
  }

  .dropdownWrapper {
    z-index: 99;
    background: #24292e;
    border: 1px solid #2C3E50;
    font-size: 14px;
  }

  .dropdownWrapper li {
    display: block;
  }

  .dropdownWrapper li:hover {
    background: #000;
  }

  .nav-item.active {
    border-radius: 5px;
    background-image: linear-gradient(90deg, #b64816, #a62cb6);
  }

  .dropdownBtn {
    display: inline-block;
    padding: 0 10px;
    width: 100%;
    height: 100%;
  }

  .nav-item {
    cursor: pointer;
  }

  .navLangChoose {
    margin-left: 20px;
    display: inline-block;
    vertical-align: middle;
  }

  /*banner图*/
  .homeBanner {
    text-align: center;
  }

  .homeBannerImg {
    width: 100%;
  }

  /*联系*/
  .contact {
    margin-top: 80px;
    padding-top: 30px;
    border-top: 1px solid #e1e4e8;
    background: #fafbfc;
  }

  /*版权*/
  .companyRights {
    text-align: center;
    color: #959595;
    font-size: 12px;
    margin: 5px 0;
  }
</style>



